.portifolio {
    box-sizing: border-box;
    /* border: solid 1px; */
    width: auto;
    margin: 20px 10vw 50px;
    font-family: 'Open Sans', sans-serif;
    border-bottom: solid 1px rgba(255, 255, 255, 0.25);
    padding: 10px 20px;
    font-size: 1em;
    background-color: white;
    border-radius: 0 30px 0 30px;
}
.portifolioTitle {
    box-sizing: border-box;
    /* border: solid 1px; */
    padding: 4px 20px;
    font-size: 1.5em;
    font-weight: 700;
    margin: 10px 0 15px;
    background-color: black;
    border-radius: 20px;
    color: white;
    width: fit-content;
}
.project {
    box-sizing: border-box;
    /* border: solid 1px; */
    margin: 10px auto 25px;
    color: black;
}
.projectTitle {
    box-sizing: border-box;
    /* border: solid 1px; */
    padding: 5px 10px;
    font-size: 1.25em;
    font-weight: 700;
    border: solid 0.5px rgba(128, 128, 128, 0.5);
    border-bottom: none;
    background-color:  rgba(128, 128, 128, 0.25);
    border-radius: 5px 5px 0 0;
}
.projectBoard {
    display: flex;
    flex-direction: row;
    margin-bottom: 15px;
    padding: 10px 0;
    background-color: white;
    border: solid 0.5px rgba(128, 128, 128, 0.5);
    border-radius: 0 0 5px 5px;
}
.projectBoardDescription {
    width: 40%;
    margin: 2px auto;
    display: flex;
    flex-direction: column;
}
.projectBoardDescriptionText {
    box-sizing: border-box;
    /* border: solid 1px; */
    padding: 5px 10px;
    margin-bottom: 10px;
}
.projectBoardDescriptionLink {
    box-sizing: border-box;
    /* border: solid 1px; */
    padding: 10px;
    font-weight: bold;
    margin: 0;
    margin-top: auto;
    text-align: center;
}
.projectBoardDescriptionLink a {
    text-decoration: none;
    background-color: rgb(46, 191, 255);
    color: white;
    padding: 10px;
    border-radius: 10px;
    border-bottom: solid 2px black;
}
.projectBoardImg {
    box-sizing: border-box;
    /* border: solid 1px black; */
    /* border-radius: 15px 15px 0 0; */
    width: 55%;
    margin: 2px auto;
    display: flex;
    flex-direction: column;
}
.projectBoardImgMain {
    width: 100%;
    height: 280px;
    padding-bottom: 2px;
    display: flex;
    flex-direction: row;
    border: solid 1px rgba(128, 128, 128, 0.25);
    border-radius: 5px 5px 0 0;
    /* background-color: black; */
    /* border-radius: 10px 10px 0 0; */
}
.projectBoardImgMainClick {
    width: 100%;
    border-radius: 5px;
}
.projectBoardImgThumbnail {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    width: 100%;
    height: auto;
    overflow-x: scroll;
    border: solid 1px rgba(128, 128, 128, 0.25);
    border-top: none;
    border-radius: 0 0 5px 5px;
    background-color: rgba(0, 0, 0, 0.25);
}
.projectBoardImgThumbnailCell {
    width:  auto;
    /* min-height: 55px; */
    margin: 2px 5px;  
}
.projectBoardImgThumbnailCellClick {
    width:  100px;
    min-height: 50px;
    border-radius: 10px;
    cursor: pointer;
    border: solid 1px rgba(128, 128, 128, 0.5);
}
/* Configuração da visualização desktop */
@media(min-width: 610.5px) {

}
/* Configuração da visualização mobile */
@media(max-width: 610px) {
    .portifolio {
        width: auto;
        margin: 20px 2vw 40px;
        font-size: 0.85em;
        padding: 5px;
    }
    .portifolioTitle {
        font-size: 1em;
    }
    .projectBoard {
        flex-direction: column-reverse;
    }
    .projectBoardDescription {
        width: 100%;
    }
    .projectBoardDescriptionTitle {
        font-size: 1.25em;
    }
    .projectBoardDescriptionText {
    }
    .projectBoardDescriptionLink {
        margin-bottom: 10px;
    }
    .projectBoardImg {
        width: 95%;
    }
    .projectBoardImgMain {
        height: 200px;
    }
    .projectBoardImgThumbnailCellClick {
        width:  60px;
        height: 40px;
    }
}