.about {
    box-sizing: border-box;
    /* border: solid 1px; */
    color: white;
    width: auto;
    margin: 20px 10vw 50px;
    font-family: 'Open Sans', sans-serif;
    border-bottom: solid 1px rgba(255, 255, 255, 0.25);
    padding: 10px 20px;
    font-size: 1em;
    /* background-color: rgb(29, 116, 245); */
    background: linear-gradient(to right, #1e3c72, #2a5298);
    border-radius: 0 30px 0 30px;
}
.aboutTitle {
    box-sizing: border-box;
    /* border: solid 1px; */
    padding: 4px 20px;
    font-size: 1.5em;
    font-weight: 700;
    margin: 10px 0;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 20px;
    color: black;
    width: fit-content;
}
.aboutContent {
    box-sizing: border-box;
    /* border: solid 1px; */
    padding: 2.5px 5px;
    margin-bottom: 15px;
}
.aboutTable {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}
.aboutTableLineImg {
    display: flex;
    flex-direction: row;
    margin-bottom: 5px;
}
.aboutTableLineImgFirstCell {
    box-sizing: border-box;
    /* border: solid 1px; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    width: 30%;
}
.aboutTableLineImgCell {
    box-sizing: border-box;
    /* border: solid 1px; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: calc( 70% / 4);
}
.aboutTableLineImgCell img {
    width: 95%;
    height: 10vw;
    border-radius: 10px 10px 0 0;
}
.aboutTableLineSubTitle {
    display: flex;
    flex-direction: row;
}
.aboutTableLineSubTitleFirstCell {
    box-sizing: border-box;
    border-bottom: solid 1px rgba(255, 255, 255, 0.5);
    text-align: center;
    width: 30%;
    color: rgb(4, 11, 12);
    background-color: white;
    font-weight: bold;
    margin: 2px 0;
    padding: 5px 0;
    border-radius: 5px 5px 0 0;
}
.aboutTableLineSubTitleCell {
    box-sizing: border-box;
    border-left: solid 2px rgba(0, 0, 0, 0.5);
    text-align: center;
    width: calc(70% / 4);
    color: rgb(4, 11, 12);
    background-color: white;
    font-weight: bold;
    margin: 2px 0;
    padding: 5px 0;
    border-radius: 5px 5px 0 0;
}
.aboutTableLineBlank {
    display: flex;
    flex-direction: row;
}
.aboutTableLineBlankFirstCell {
    box-sizing: border-box;
    border-bottom: solid 1px rgba(255, 255, 255, 0.7);
    text-align: center;
    width: 30%;
    font-weight: bold;
    padding: 5px 0;
    border-radius: 0 0 0 5px
}
.aboutTableLineBlankCell {
    box-sizing: border-box;
    border-left: solid 0.5px rgba(255, 255, 255, 0.1);
    border-bottom: solid 1px rgba(255, 255, 255, 0.7);
    text-align: center;
    width: calc(70% / 4);
    padding: 5px 0;
}
/* Configuração da visualização desktop */
@media(min-width: 610.5px) {

}
/* Configuração da visualização mobile */
@media(max-width: 610px) {
    .about {
        width: auto;
        margin: 20px 2vw 40px;
        font-size: 0.85em;
        padding: 5px;
    }
    .aboutTitle {
        padding: 4px 20px;
        font-size: 1em;
        margin: 10px 0;
        border-radius: 20px;
    }
    .aboutTableLineImgFirstCell {
        width: 25%;
    }
    .aboutTableLineImgCell {
        width: calc(75% / 4);
    }
    .aboutTableLineImgCell img {
        height: 12vw;
    }
    .aboutTableLineSubTitleFirstCell {
        width: 25%;
        font-size: 0.75em;
    }
    .aboutTableLineSubTitleCell {
        width: calc(75% / 4);
        font-size: 0.5em;
    }
    .aboutTableLineBlankFirstCell {
        width: calc(25%);
        font-size: 0.75em;
    }
    .aboutTableLineBlankCell {
        width: calc(75% / 4);
        font-size: 0.75em;
    }
}