.contact {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    /* border: solid 1px; */
    width: calc(100vw - 20vw);
    width: auto;
    margin: 20px 10vw 50px;
    font-family: 'Open Sans', sans-serif;
    padding: 10px 20px 50px;
    font-size: 1em;
    /* background-color: rgb(11, 24, 46); */
    background: linear-gradient(to right, #1e3c72, #2a5298);
    border-radius: 0 30px 0 30px;
}
.contactBoard {
    display: flex;
    flex-direction: column;
}
.contactBoardTitle {
    box-sizing: border-box;
    /* border: solid 1px; */
    padding: 4px 20px;
    font-size: 1.5em;
    font-weight: 700;
    margin: 10px 0 15px;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 20px;
    color: black;
    width: fit-content;
}
.contactBoardImg img {
    width: 150px;
    height: 150px;
    border-radius: 100px;
    margin: 10px 2.5px 0;
}
.contactBoardMsg {
    color: white;
    margin: 10px 2.5px;
}
.contactBoardLine {
    padding: 2.5px;
    color: white;
    font-size: 30px;
}
.contactBoardLineSpan {
    font-size: 15px;
    color: darkgray;
}
.contactBoardImg {
    box-sizing: border-box;
    /* border: solid 1px; */
    width: auto;   
}
/* Configuração da visualização desktop */
@media(min-width: 610.5px) {
}
/* Configuração da visualização mobile */
@media(max-width: 610px) {
    .contact {
        width: auto;
        margin: 20px 2vw 25px;
        font-size: 0.85em;
        padding: 5px 5px 25px;
    }
    .contactBoardTitle {
        font-size: 1em;
    }
    .contactBoardImg img{
        width: 125px;
        height: 125px;
        
    }
    .contactBoardLine {
        padding: 5px;
        color: white;
        font-size: 1.5em;
    }

}